            span{
              font:bold 10px/1 'Orbitron';
              color:rgba(255,255,255,.9);
              letter-spacing: 2.3px;
              text-align: right;
              display: block;
            }
          }

          &:nth-child(2n+1){
            display: flex;
            flex-direction: row-reverse;

            .picture{
              strong{
                left:570px;
