  .loadingAni{
    position: relative;
    top: 250px;

    img{
      position: absolute;
      perspective: 800;
      opacity: 0;

      &:nth-child(1){
        width:300px;
        height: 200px;
        transform: rotateY(-180deg);
        position: absolute;
        left: 250px;
        animation: galleryAni01 10s;
      }
      
      &:nth-child(2){
        width:10px;
        height: 10px;
        animation: galleryAni02 9s 1s;
      }

      &:nth-child(3){
        width:15px;
        height:15px;
        animation: galleryAni03 7s 3s;
        animation-delay: 3s;
      }
    }
  }

    .innerWrap {
      transform: translateY(200px);
      opacity: 0;
      transition: 1s;
      position: absolute;
      width: 100%;
      bottom: -50px;
      height: 1000px;

      >.inner {
        transition: none;
        width: 1180px;
        margin: 0px auto;
        position: relative;
        border:1px solid rgba(0,0,0, .01);
    

        .searchBox{
          position: absolute;
          top: 25px;
          right: 10px;

          input[type=text]{
            width: 167px;
            height: 35px;
            font-family: 'Orbitron';
            font-size: 10px;
            padding-left:5px;
            letter-spacing: 1.2px;
          }

          button{
            width: 50px;
            height: 35px;
            font-family: 'Orbitron';
            color:#fff;
            font-size: 10px;
            background: rgba(0,0,0,.5);
            border:none;
            cursor: pointer;
            letter-spacing: 1.2px;
          }
        }

        .loading {
          position: absolute;
          top:0px;
          left: 50%;
          width: 50%;
          transform: translateX(-50%);
        }

        >section {
          transform: translateY(200px);
          opacity: 0;
          transition: 1s;
          margin-top: 75px;
          margin-bottom:50px;
    
          &.on {
            transform: translateY(0px);
            opacity: 1;
          }
    
          &::after {
            content: '';
            display: block;
            clear: both;
          }
    
          article {
            width: 20%;
            height: 250px;
            float: left;
            padding: 10px;
    
            >.inner {
              width: 100%;
              height: 100%;
              background: #fff;
              box-shadow: 5px 5px 10px rgba(0,0,0, .1);
              padding: 10px;
    
              .pic {
                width: 100%;
                height: 80%;
                margin-bottom: 10px;
                cursor: pointer;
    
                img {
                  width: 100%; 
                  height: 100%;
                }            
              }
    
              >b {
                width: 100%;
                height: 20%;
                display: block;
                font:normal 10px/1 'Orbitron';
                color:#555;
                letter-spacing: 2.5px;
                margin-bottom: 20px;
              }
            }
          }
        }
      } 
    } 
    
    &.on {
      .innerWrap {
        transform: translateY(0px); opacity: 1;
      }
    }
