  .popup {
    width: 100%;
    height: 100%;
    background: rgba(0,0,0,.9);
    position: fixed;
    top: 0px;
    left: 0px;
    padding: 5vw;
    z-index: 6;
  
    img {
      width: 100%;
      height: 100%;
      object-fit: contain;
    }
  
    span {
      font: bold 14px/1 'arial';
      color: #fff;
      position: absolute;
      top: 4vh;
      right: 5vw; cursor: pointer;
    }
  }
  

  @keyframes galleryAni01{
    0%{left:250px; opacity: 0;}
    10%{opacity: 0.5;}
    90%{ opacity: 0.5;}
    100%{ left: 500px; opacity: 0;}
  }

  @keyframes galleryAni02{
    0%{left: 485px; top:65px; opacity: 0;}
    90%{opacity: 0.5;}
    100%{left: 700px; top:-10px; opacity: 0;}
  }

  @keyframes galleryAni03{
    0%{left: 590px; top:70px; opacity: 0;}
    90%{opacity: 0.5;}
    100%{left:700px; top:20px;opacity: 0;}
  }


  @media screen and (max-width: $tablet){
    .content {
      >figure {
        height: 200px;
      }
  
      .innerWrap {
        >.inner {
          width: 100%;
          padding: 0px 3vw;
          padding-top: 80px
        }
      }
      
    }
  }
  
  @media screen and (max-width: 1179px){
    .gallery {
      .item {width: 25%!important;}
    }
  }
  
  @media screen and (max-width: 899px){
    .gallery {
