@import url('https://fonts.googleapis.com/css2?family=Shippori+Antique+B1&family=Zhi+Mang+Xing&display=swap');

#map{
    width: 100%;
    height: 100vh;
    background-image: url(../../../public/img/mapBg01.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    padding: 100px 0px;
    filter: saturate(70%);

    .inner{
        margin: 0 auto;
        width: 1180px;
        height: 100%;
        position: relative;
        border:1px solid rgba(255,255,255,.1);
        background: #1e2831;
        padding-left: 45px;
        padding-top: 20px;

        .wrap{
            width: 100%;
            height: 700px;
            padding:50px 0px;
            display: flex;
            flex-direction: row;

            .mapList{
                width: 50%;
                height: 100%;
                padding:50px 0px;

                #maps{
                    width: 100%;
                    height: 100%;
                    filter: grayscale(100%);

                    .mapBox{
                        div{

                        }
                    }
                }
            }

            .contactForm{
                width: 50%;
                height: 100%;
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: flex-start;
                padding-left: 75px;


                h2{
                    font:bold 50px/1 'Orbitron';
                    color:rgba(255,255,255,.9);
                    letter-spacing:5px;
                    margin-bottom: 30px;
                }

                ul{
                    text-align: left;
                    margin-bottom: 30px;

                    li{
                        strong{
                            font:bold 15px/1 'Orbitron';
                            color:rgba(255,255,255,.8);
                            letter-spacing:2.3px;
                            margin-bottom: 15px;
                            display: block;
                        }

                        p{
                            font:normal 12px/1 'Orbitron';
                            color:rgba(255,255,255,.7);
                            letter-spacing:2.3px;
                            margin-bottom: 10px;
                        }

                        span{
                            font:normal 12px/1 'Orbitron';
                            color:rgba(255,255,255,.7);
                            letter-spacing:2.3px;
                        }
                    }
                }
                
                h3{
                    font:bold 25px/1 'Orbitron';
                    color:rgba(255,255,255,.8);
                    letter-spacing:5px;
                    margin-bottom: 25px;
                }

                form{
                    width: 85%;
                    
                    tbody{
                        width: 100%;
                        height: 100%;

                        tr{

                            td{

                                input{
                                    width: 450px;
                                    height: 40px;
                                    margin-bottom: 20px;
                                    font:bold 10px/1 'Orbitron';
                                    letter-spacing:1.8px;
                                    color:#111;
                                    padding:10px;
                                }

                                textarea{
                                    width: 450px;
                                    margin-bottom: 15px;
                                    font:bold 10px/1 'Orbitron';
                                    letter-spacing:1.8px;
                                    color:#111;
                                    padding:10px;
                                }
                            }

                            .btnSet2{

                                input{
                                    width: 100px;
                                    height: 30px;
                                    border:none;
                                    background: rgba(255,255,255,.9);
                                    color:rgba(0,0,0,.9);
                                    cursor: pointer;
                                    font:bold 11px/1 'Orbitron';
                                    letter-spacing:1.8px;
                                    transition: 1s;

                                    &:hover{
                                        background: rgba(0,0,0,.9);
                                        color:rgba(255,255,255,.9);
                                    }
                                }
                            }
                        }
                    }
       
                }
            }
        }
    }
}

