#video{
    width: 100%;
    height: 100vh;
    background-image: url(../../../public/img/videoBg01.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    padding: 150px 0px 0PX;
    // border:1px solid #fff;
    position: relative;
    filter: saturate(70%);
    z-index:10;

    .explanation07{
    position: absolute;
    right: 27.5%;
    bottom:-5%;
    width: 200px;
    height: 200px;
    z-index:10;

    .exP7{
        font:normal 10px 'Orbitron';
        color:rgba(255,255,255,.5);
        letter-spacing: 2px;
        color:#fff;
        width: 200px;
        height: 100%;
        display: block;
        position: absolute;
        transform: rotate(-90deg);

    &::before{
        content: '';
        display: block;
        width: 2px;
        height: 80%;
        background: rgba(255,255,255,.5);
        position: absolute;
        bottom:45%;
        right:90%;
        transform: rotate(90deg);
        }

      &::after{
          content: '∨';
          display: block;
          font-size: 20px;
          color:rgba(255,255,255,.5);
          position: absolute;
          bottom:78%;
          right:125%;
          transform: rotate(-270deg);
          }
      }
  }


    >strong{
        font:bold 250px/1 'Orbitron';
        color:rgba(255,255,255,.03);
        letter-spacing: 3px;
        position: absolute;
        right: 50px;
        top:-150px;

        b{
          font-size: 300px;
          line-height:200px;
        }
        
    }

    .inner{
        width: 100%;
        margin: 0 auto;

        #navi{
            width: 35%;
            height: 100%;
            position: absolute;
            top: 0px;
            right: 0px;
            // background: rgba(0,0,0,.2); backdrop-filter: blur(5px); 
            z-index: 4;
            overflow: hidden;

            h3{
              font:bold 100px/1 'Orbitron';
              color:rgba(255,255,255,.8);
              letter-spacing: 7px;
              position: absolute;
              left: 65%;
              top: 15%;
              transform: translateX(-50%);
            }

            ul{
                width: 100%;
                position: absolute;
                top: 65%;
                left: 0px;
                transform: translateY(-50%);

                li {
                    width: 100%;
                    padding: 50px 100px;
                    position: relative;
                    overflow: hidden;
                    opacity: 0.4;
                    cursor: pointer;
                    transition: 1.4s;
                    transform-origin: left center;

                    h2 {
                        font: bold 12px/1 'orbitron';
                        color: #fff;
                        letter-spacing: 1px;
                        margin-bottom: 5px;

                        &::after {
                            content: '';
                            display: block;
                            width: 20px;
                            height: 1px;
                            background: #fff;
                            margin-top: 5px;
                            transition: 1.4s;
                        }
                    }
                    p {
                        font: 10px/1 'orbitron';
                        color: #fff;
                        letter-spacing: 0.5px;
                        opacity: 0.7;
                    }

                    &.on {
                            background: linear-gradient(to right, rgba(0,0,0,.4), rgba(0,0,0,0));
                            transform: scale(1.3); opacity: 1;

                        h2::after {
                            width: 150px;
                        }
                    }
                }
            }
        }

        .masks{
            width: 75%;
            height: 100%;
            position: absolute;
            top: 0;
            left: 0;

            >div{
                width: 100%;
                height: 100%;
                position: absolute;
                top: 0px;
                left: 0px;
                z-index:1;

                video{
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                    transform: rotateY(180deg);
                    filter: saturate(15%);
                }

                .slogan {
                    position: absolute;
                    right: 8%;
                    top: 50%;
                    transform: translateY(-60%);
                    text-align: right; padding: 50px;
                    perspective: 700px;
              
                    &::before {
                        content: '';
                        display: block;
                        width: 500px;
                        height: 500px;
                        border: 1px solid #fff;
                        position: absolute;
                        right: 0px;
                        top: -50px;
                        transition: 0.5s 1.4s;
                        transform: rotateY(-100deg); opacity: 0;
                    }
              
                    h2 {
                        display: inline-block;
                        overflow: hidden;
                        position: relative;
                        margin-bottom: 20px;
              
                      .title {
                        font: normal 200px/1 'Abril Fatface';
                        color: #fff;
                        position: relative;
                        transform: scaleY(1.25);
                        opacity: 0;
                        transition: opacity 0.2s 0.5s;
                      }
              
                      &::after {
                        content: '';
                        display: block;
                        width: 100%;
                        height: 100%; 
                        background: #fff;
                        position: absolute;
                        top: 0px;
                        right: -100%;
                        transition: right ease-in-out 1s; 
                      }
                    }
              
                    .p1 {
                      display: inline-block;
                      overflow: hidden; 
                      position: relative;
                      margin-right: 14px;
              
                      .p01 {
                        font: 20px/1 'verdana';
                        color: #fff;
                        position: relative;
                        opacity: 0;
                        transition: opacity 0.2s 0.8s;
                        transform: scaleY(1.2);
                      }
              
                      &::after {
                        content: '';
                        display: block;
                        width: 100%;
                        height: 100%;
                        background: #fff;
                        position: absolute;
                        top: 0px;
                        right: -100%; 
                        transition: right ease-in-out 1s 0.3s;
                      }
                    }
              
                    .p2 {
                      display: inline-block;
                      overflow: hidden;
                      position: relative;
                      margin-right: 14px;
              
                      .p02 {
                        font: 20px/1 'verdana';
                        color: #fff;
                        position: relative;
                        opacity: 0;
                        transition: opacity 0.2s 1.1s;
                        transform: scaleY(1.2);
                      }
              
                      &::after {
                        content: '';
                        display: block;
                        width: 100%;
                        height: 100%;
                        background: #fff;
                        position: absolute;
                        top: 0px;
                        right: -100%; 
                        transition: right ease-in-out 1s 0.6s;
                      }
                    }
                }

                &.on {
                    z-index: 3;
              
                    // .slogan {
                    //   &::before {
                    //     transform: rotateY(-30deg); opacity: 1;
                    //   }
                    //   h2, .p1, .p2{
                    //     .title, .p01, .p02 {opacity: 1;}
                    //     &::after {right: 100%;}
                    //   }
                    // }
                    &:nth-of-type(1).mask {
                      -webkit-mask: url(../../../public/img/mask1.png);
                      -webkit-mask-size: 2300% 100%;
                      animation: mask-play 1.4s steps(22) forwards;
                    }
                    &:nth-of-type(2).mask {
                        -webkit-mask: url(../../../public/img/mask2.png);
                      -webkit-mask-size: 3000% 100%;
                      animation: mask-play 1.4s steps(29) forwards;
                    }
                    &:nth-of-type(3).mask {
                        -webkit-mask: url(../../../public/img/mask3.png);
                      -webkit-mask-size: 7100% 100%;
                      animation: mask-play 1.4s steps(70) forwards;
                    }
                    &:nth-of-type(4).mask {
                        -webkit-mask: url(../../../public/img/mask4.png);
                      -webkit-mask-size: 7400% 100%;
                      animation: mask-play 1.4s steps(73) forwards;
                    }
                  }

                &.lower {
                z-index: 2;
                }
            }
        }
    }

    .page03{
        margin-left: 30px;
        position: absolute;
        bottom: 1%;

        strong{
            font:bold 15px/1 'Orbitron';
            color:rgba(255,255,255,.9);
            letter-spacing: 3px;
        }
    }
}

@keyframes mask-play {
    0% {-webkit-mask-position: 0% 0%;}
    100% {-webkit-mask-position: 100% 0%;}
  }








