#about{
    width: 100%;
    height: 100vh;
    padding: 150px 0px 0px;
    // border:1px solid red;
    margin-top: -100vh;
    position: relative;
    overflow: hidden;

    >strong{
        font:bold 250px/1 'Orbitron';
        color:rgba(255,255,255,.03);
        letter-spacing: 3px;
        position: absolute;
        transform: rotate(270deg);
        left:-200px;
        top: 25%;   
    }

    .explanation06{
        position: absolute;
        right:255px;
        bottom: 50px;
        width: 200px;
        height: 200px;

        .exP6{
            font:normal 10px 'Orbitron';
            color:rgba(255,255,255,.5);
            letter-spacing: 1.2px;
            color:#fff;
            width: 200px;
            height: 100%;
            display: block;
            position: absolute;
            transform: rotate(90deg);

        &::before{
            content: '';
            display: block;
            width: 2px;
            height: 80%;
            background: rgba(255,255,255,.5);
            position: absolute;
            bottom:45%;
            right:20%;
            transform: rotate(90deg);
            }

        &::after{
            content: '∨';
            display: block;
            font-size: 20px;
            color:rgba(255,255,255,.5);
            position: absolute;
            bottom:78.5%;
            right:-22%;
            transform: rotate(270deg);
            }
        }
    }

    .inner{
        width: 100%;
        margin: 0 auto;

        .wrap{
            width: 1180px;
            height: 100%;
            margin: 0 auto;
            // border:1px solid yellow;
            padding:50px 250px;

            h2{
                font:normal 100px/1 'Orbitron';
                color:rgba(255,255,255,.9);
                letter-spacing: 5px;
                line-height: 130px;
                margin-bottom: 20px;
                b{
                    font-size:130px;
                }
            }

            >p{
                position: relative;
                left: 62.5%;
                transform: translateX(-50%);
                font:normal 10px/17.5px 'Orbitron';
                color:rgba(255,255,255,.7);
                letter-spacing: 1.2px;
                width: 80%;

                &:nth-child(3){
                    margin-top: 30px;
                    margin-bottom: 30px;
                }
            }
        }
    }

    .page02{
        margin-left: 30px;
        position: absolute;
        bottom: 1%;

        strong{
            font:bold 15px/1 'Orbitron';
            color:rgba(255,255,255,.9);
            letter-spacing: 3px;
        }
    }
}