#member{
    width: 100%;
    height: 100vh;
    background-image: url(../../../public/img/infoBg01.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    filter: saturate(70%);
    padding:0px 50px;
    position: relative;
    z-index:20;

    .explanation09{
        position: absolute;
        left: 350px;
        bottom: -50px;
        width: 200px;
        height: 200px;

        .exP9{
            font:normal 10px 'Orbitron';
            color:rgba(255,255,255,.5);
            letter-spacing: 2px;
            color:#fff;
            width: 200px;
            height: 100%;
            display: block;
            position: absolute;
            transform: rotate(-90deg);
  
        &::before{
            content: '';
            display: block;
            width: 2px;
            height: 80%;
            background: rgba(255,255,255,.5);
            position: absolute;
            bottom:45%;
            right:90%;
            transform: rotate(90deg);
            }
  
          &::after{
              content: '∨';
              display: block;
              font-size: 20px;
              color:rgba(255,255,255,.5);
              position: absolute;
              bottom:78%;
              right:125%;
              transform: rotate(-270deg);
              }
          }
      }

    strong{
        font:bold 200px/1 'Orbitron';
        color:rgba(255,255,255,.03);
        letter-spacing: 15px;
        text-align: right;
        display: block;

        >b{
            font:bold 250px/130px 'Orbitron';
        }
    }

    h2{
        font:bold 85px/1 'Orbitron';
        color:rgba(255,255,255,.8);
        letter-spacing: 7px;
        position: absolute;
        top: 7.5%;
        left: 52.5%;

    }

    >p{
        font:normal 17px/1 'Orbitron';
        color:rgba(255,255,255,.5);
        letter-spacing: 2px;
        position: absolute;
        top: 17.5%;
        left: 52.9%;
    }

    .inner{
        width: 80%;
        position: relative;
        margin: 0 auto;

        .wrap{
            width: 100%;

            .memberMain{
                width: calc((100% - 300px) / 4);
                margin-right: 100px;
                height: 500px;
                float: left;
                border:1px solid rgba(255,255,255,.5);
                position: relative;
                cursor: pointer;

                &:nth-child(1){
                    top: -50px;
                }

                
                &:nth-child(2){
                    top: 75px;
                }
                
                &:nth-child(3){
                    top: 150px;
                }

                &:nth-child(4){
                    margin-right: 0;
                    top: 7.5px;
                }

                img{
                    width: 100%;
                    height: 415px;
                    object-fit: cover;
                    position: relative;
                    top: -30px;
                    right: -30px;
                    box-shadow: 3px 3px 5px rgba(0,0,0,.9);
                    filter: saturate(60%);
                }

                h3{
                    font:bold 15px/1 'Orbitron';
                    color:rgba(255,255,255,.9);
                    letter-spacing: 2.3px;
                    margin-bottom: 20px;
                    margin-left: 30px;
                    margin-top: -10px;
                }

                p{
                    font:normal 11px/1 'Orbitron';
                    color:rgba(255,255,255,7);
                    letter-spacing: 1.8px;
                    margin-bottom: 15px;
                    margin-left: 30px;
                }

                span{
                    font:normal 10px/1 'Orbitron';
                    color:rgba(255,255,255,7);
                    letter-spacing: 1.8px;
                    margin-left: 30px;
                    display: block;
                }
            }
        }
    }

    .custom-shape-divider-top-1646325685 {
        position: absolute;
        bottom:25px;
        left: 15%;
        width: 75%;
        overflow: hidden;
        line-height: 0;
        transform: rotate(2.5deg);
        z-index:-1;
    }
    
    .custom-shape-divider-top-1646325685 svg {
        position: relative;
        display: block;
        width: calc(92.5% + 1.3px);
        height: 175px;
    }
    
    .custom-shape-divider-top-1646325685 .shape-fill {
        fill: rgba(0,0,0,.04)
    }
}