@import url('https://fonts.googleapis.com/css2?family=Dancing+Script&display=swap');

.department{
    
    figure{
        width: 100%;
        height: 100%;
        position: relative; 
        margin-top: -100px;
        overflow: hidden;
        background-image: url(../../../public/img/aboutBg01.jpg);
        background-size: cover;
        background-position:right bottom;
        background-repeat: no-repeat;
        filter: saturate(30%);
        // video{
        //     width:100%;
        //     height:100%;
        //     object-fit: cover;
        // }

        img{
            width:100%;
            height:100%;
            object-fit: cover;
            filter:saturate(20%);
        }
        
        .aboutBg{
            font:bold 500px/1 'Orbitron';
            color:rgba(255,255,255,.07);
            letter-spacing: 25px;
            position: relative;
            top: 100px;
            left: 80%;
            transform: translateX(-50%);
            font-style: italic;
            // border:1px solid red;

            strong{
                position: relative;
                top: 100px;

                b{
                    position: relative;
                    top: -75px;
                    color:rgba(255,255,255,.15);
                    text-shadow: 5px 5px 10px rgba(255,255,255,.1);
                }
            }
        }
    
        .aboutContent{
            position: relative;
            top: 17.5%;
            left:27.5%;
            transform: translate(-50%,-50%);
            width: 35%;

            h1{
                font:bold 65px/1 'Orbitron';
                color:#fff;
                letter-spacing: 10px;
                margin-bottom: 20px;
                text-shadow: 5px 5px 7px rgba(0,0,0,.5);
            }

            button{
                width: 170px;
                height: 45px;
                background: #111;
                font:normal 11px/1 'Orbitron';
                color:#fff;
                letter-spacing: 1.2px;
                margin-bottom: 100px;
                cursor: pointer;
            }
        
            b{
                display: block;
                font:bold 12px/1 'Orbitron';
                color:#eee;
                letter-spacing: 1.7px;
                transform: rotate(35deg);
            }
        }

        .aboutList{
            position: absolute;
            top: 13.5%;
            left: 5.5%;
            transform: translate(-50%,-50%);
            // border:1px solid red;

            >ul{

                li{
                    font:normal 30px/35px 'Orbitron';
                    color:#fff;
                    letter-spacing: 2.3px;
                    text-align: center;
                    margin-bottom: 45px;

                    >b{
                        font:bold 75px/1 'Orbitron';
                        color:#fff;
                        letter-spacing: 3px;
                    }
                }
            }
        }

        .aboutUs{
            width: 100%;
            height: 100%;
            position: relative;
            top:50%;
            margin-bottom: 150px;
            padding:175px 30px 100px 150px;
            display: flex;
            flex-direction: row;

            h2{
                position: absolute;
                top: 10px;
                right:30px;
                font:bold 100px/1 'Orbitron';
                color:rgba(255,255,255,.8);
                letter-spacing: 5px;

                &:before{
                    content: '';
                    display: block;
                    position: absolute;
                    width: 40%;
                    height: 2px;
                    background: #fff;
                    top: -25px;
                    left: -50px;
                }
            }

            .content{
                width: 40%;
                padding:150px 0px 100px;
                
                h3{
                    font:bold 35px/1 'Orbitron';
                    color:rgba(255,255,255,.8);
                    letter-spacing: 3.3px;
                    margin-bottom: 30px;
                }

                >p{
                    font:normal 12px/25px 'Orbitron';
                    color:#ddd;
                    letter-spacing: 1.5px;
                    margin-bottom: 30px;
                }

                span{
                    font:normal 10px/1 'Orbitron';
                    color:#ccc;
                    letter-spacing: 1.5px;
                    margin-bottom: 25px;
                    display: block;
                }

                b{
                    text-align: right;
                    display: block;
                    font:bold 20px/1'Dancing Script';
                    color:#eee;
                    letter-spacing: 3px;
                }
            }

            .aboutPic{
                width: 60%;
                position: relative;
                padding:150px 30px 100px;

            &::before{
                content: '';
                display: block;
                width: 3px;
                height: 80%;
                background: rgba(255,255,255,.7);
                position: absolute;
                left: 100px;
                bottom: -50px;
            }

            &::after{
                content: '';
                display: block;
                width:60%;
                left: 20%;
                height: 3px;
                background: rgba(255,255,255,.7);
                position: absolute;
                bottom: 0;
            }

                img{
                    object-fit: cover;
                    transition: 1s;

                    &:hover{
                        filter: saturate(100%);
                        transform: translateY(-30px);
                    }

                    &:nth-child(1){
                        width: 350px;
                        height: 500px;
                        filter: saturate(70%);
                        position: absolute;
                        top: 195px;
                        z-index:3;
                        right: 480px;
                    }

                    &:nth-child(2){
                        position: absolute;
                        right: 130px;
                        top: 60px;
                        z-index:1;
                        width: 375px;
                        height: 475px;
                        filter: saturate(70%);
                    }

                    &:nth-child(3){
                        position: absolute;
                        right: 30px;
                        z-index:2;
                        top: 500px;
                        width: 500px;
                        height: 300px;
                        filter: saturate(70%);
                    }
                }
            }
        }

        .aboutService{
            width: 100%;
            position: relative;
            max-height: 700px;
            top: 55%;
            padding:150px 0px 0px;
            overflow: hidden;

            h2{
                position: absolute;
                top:10px;
                left:0px;
                font:bold 100px/1 'Orbitron';
                color:rgba(255,255,255,.8);
                letter-spacing: 5px;
            }

            .swiper{

                .swiper-button-prev{
                    transition: 0.5s;
                    color:#fff;

                    &:hover{
                        color:#111;
                    }

                    &::after{
                        content: '◀';
                    }
                }

                .swiper-button-next{
                    color:#fff;
                    transition: 0.5s;

                    &:hover{
                        color:#111;

                    }

                    &::after{
                        content: '▶';
                    }
                }

                .swiper-wrapper{
                    .swiper-slide{
                        display: flex;
                        
                        .servicePic{
                            width: 40%;
                            
                            img{
                                width: 100%;
                                height: 500px;
                                object-fit: cover;
                                filter: saturate(60%);
                            }
                            video{
                                width: 300px;
                                height: 300px;
                                object-fit: cover;
                                position: absolute;
                                left: 40%;
                                transform: translateX(-50%);
                                z-index:5;
                                margin-top: 175px;
                                filter: saturate(80%);
                            }
                        }
            
                        .serviceContent{
                            width: 60%;
                            background: rgba(0,0,0,.3);
                            padding:0px 50px 0px  250px;
                            
                            b{
                                font:bold 50px/1 'Orbitron';
                                color:rgba(255,255,255,.9);
                                margin-bottom: 30px;
                                margin-top: 50px;
                                display: block;
                            }
            
                            p{
                                font:normal 10px/20px 'Orbitron';
                                color:rgba(255,255,255,.7);
                                letter-spacing: 2.3px;
                                 margin-bottom: 20px;
                            }

                            span{
                                font:bold 10px/1 'Orbitron';
                                color:rgba(255,255,255,.9);
                                letter-spacing: 3px;
                                display: block;
                                margin-top:50px;
                                margin-bottom: 30px;
                                text-align: right;
                            }

                            button{
                                font:bold 11px/1 'Orbitron';
                                letter-spacing: 2.3px;
                                width: 130px;
                                height: 35px;
                                padding: 10px;
                                border:none;
                                background: rgba(0,0,0,.3);
                                color:rgba(255,255,255,.7);
                                cursor: pointer;
                                transition: 0.5s;

                                &:hover{
                                    background: rgba(0,0,0,.4);
                                    color:rgba(255,255,255,.9);
                                }
                            }
                        }
                    }
                }
            }
        }


        .etc{
            width: 100%;
            height: 100%;
            position: relative;
            top: 0;
            margin: 0 auto;
            margin-bottom: 100px;
            padding:0px 100px;

            >ul{
                display: flex;
                flex-direction: row;
                justify-content: space-between;

                li{
                    padding:50px 15px 50px 50px;
                    text-align: center;
                    position: relative;

                    &:nth-child(1){
                        &::after{
                            display: none;
                        }
                    }
                    
                    &::after{
                        content: '';
                        display: block;
                        width: 50px;
                        position: absolute;
                        height: 1px;
                        background: #fff;
                        position: absolute;
                        top: 20%;
                        left: 0;
                    }

                    .svg-inline--fa{
                        font-size: 30px;
                        color:rgba(255,255,255,.6);
                        margin-bottom: 30px;
                        padding:10px;
                        border:1px solid #eee;
                        border-radius: 50%;
                        cursor: pointer;
                        transition: 0.5s;

                        &:hover{
                            color:rgba(255,255,255,.9);
                            transform: scale(1.1);
                            box-shadow: 3px 3px 5px rgba(255,255,255,.1);
                        }
                    }

                    span{
                        font:bold 11px/1 'Orbitron';
                        color:rgba(255,255,255,.8);
                        letter-spacing: 3px;
                        line-height:15px;
                        margin-bottom: 20px;
                        display: block;

                        b{
                            font:bold 15px/1 'Orbitron';
                            color:rgba(255,255,255,.9);
                            letter-spacing: 3px;
                            margin-right: 10px;
                        }
                    }

                    >p{
                        font:normal 10px/20px 'Orbitron';
                        color:rgba(255,255,255,.7);
                        letter-spacing: 2.3px;
                    }
                }
            }
        }

        .inner2{
            width: 100%;
            height: 100%;
            position: relative;
            margin: 0 auto;
            padding-bottom: 50px;
            margin-bottom: 50px;

            h2{
                font:bold 100px/1 'Orbitron';
                color:rgba(255,255,255,.8);
                letter-spacing: 5px;
                text-align: center;
                margin-bottom: 100px;
            }

            >button{
                position: absolute;
                top: 100px;
                right: 50px;
                width: 150px;
                height: 35px;
                border:none;
                font:bold 10px/1 'Orbitron';
                color:rgba(255,255,255,.7);
                letter-spacing: 1.8px;
                background: rgba(0,0,0,.5);
                cursor: pointer;
                transition: 0.5s;

                &:hover{
                    color:rgba(255,255,255,.9);
                    background: rgba(0,0,0,.7);
                }
            }

            .memberIntro{
                width: 1180px;
                margin: 0 auto;
                display: flex;
                justify-content:space-between;
                align-items: center;
                flex-wrap: wrap;

                >article{
                    width: 30%;
                    height: 500px;
                    padding:15px 15px 100px;
                    background: rgba(255,255,255,.03);
                    box-shadow: 5px 5px 10px rgba(0,0,0,.9);
                    margin-bottom: 100px;
                    transition: 1s;

                    &:hover{
                        background: #fff;
                        transform: scale(1.1);
                        transform: translateY(-30px);
                        
                        h3,p,div{
                            color:#111;
                        }

                        img{
                            filter: saturate(100%);
                        }
                    }

                    img{
                        width: 100%;
                        height: 100%;
                        object-fit: cover;
                        margin-bottom: 15px;
                    }
                    
                    >h3{
                        text-align: center;
                        font:bold 11px/1 'Orbitron';
                        color:rgba(255,255,255,.8);
                        letter-spacing: 2.3px;
                        margin-bottom: 15px;
                    }

                    p{
                        text-align: center;
                        font:bold 10px/1 'Orbitron';
                        color:rgba(255,255,255,.7);
                        letter-spacing: 5px;
                    }

                    >div{
                        display: flex;
                        width: 50%;
                        margin: 0 auto;
                        justify-content: space-evenly;
                        align-items: center;
                        margin-top: 20px;
                        font-size: 20px;
                        color:rgba(255,255,255,.5);
                    }
                }
            }

        }
    }
}


