@import url('https://fonts.googleapis.com/css2?family=Orbitron&family=Playfair+Display:ital@0;1&display=swap');


header{
    width:100%;
    letter-spacing: 1.3px;
    border-bottom:3px solid rgba(255,255,255,.3);
    height: 100px;
    position: relative;
    z-index:5;


    .inner{
        width:1180px;
        height:100px;
        margin:0px auto;
        position:relative;
        z-index:3;


        h1{
            position:absolute;
            left:0px;
            bottom:20px;

            a{
                font:bold 24px/1 'Orbitron';
                color:#fff;
            }
        }

        #gnb{
            position:absolute;
            bottom:0px;
            right:0px;

            li{
                float:left;
                a{
                    display:inline-block;
                    padding:20px 30px;
                    font:14px/1 'Orbitron';
                    color:#fff;
                    width: 100%;
                    position: relative;

                    &:hover{

                        &::after{
                            content: '';
                            display: block;
                            height: 3px;
                            position: absolute;
                            background: #fff;
                            bottom: 0px;
                            right: 50%;
                            animation: locationAni 1s;
                            animation-fill-mode: forwards;
                        }
    
                        &::before{
                            content: '';
                            display: block;
                            height: 3px;
                            position: absolute;
                            background: #fff;
                            bottom: 0px;
                            left:50%;
                            animation: locationAni 1s;
                            animation-fill-mode: forwards;
                        }
                    }
    
                }
            }
        }

        .fa-bars{
            font-size:20px;
            color:#fff;
            position:absolute;
            bottom:20px;
            right:5vw;
            display:none;
            cursor: pointer;
        }
    }

    &.main{
        background: transparent;
        position: absolute;
        top: 0;
        height: 100px;
        width: 100%;
        left: 0;
        z-index:5;
        border:none;

        .inner{
            width: 100%;
            height: 100%;
            position: relative;

            h1{
                left: 10vw;
            }

            #gnb{right: 10vw;}
        }
    }
}

#menuMore{
    width: 280px;
    height: 100vh;
    background: rgba(0,0,0,.95);
    position: fixed;
    top:0px;
    left:-280px;
    z-index:7;
    transition: 0.5s;

    ul li a{
        font:bold 20px/1 'arial';
        color:orange;
        padding:20px;
        display: inline-block;
    }

    &.on{
        left:0px;
    }
}

@keyframes locationAni{
    0%{width: 0%;}
    100%{width: 50%;}
}

@media screen and (max-width : $tablet){
    header{

        .inner{
            width:100%;
    
            h1{
                left:5vw;
            }
    
            #gnb{
                display:none;
            }

            .fa-bars{
                display:block;
            }
        }
    }    
}