            letter-spacing: 7px;
            position: absolute;
            left: 10px;
            top: 17%;
            transform: translateY(-50%);
        }

        .clickBtn{
            display: flex;
            flex-direction: row;
            justify-content: center;
            align-items: center;
            position: absolute;
            right: 0;
            top: 150px;

            li{
                margin-right: 50px;
                font:normal 12px 'Orbitron';
                color:rgba(255,255,255,.7);
                letter-spacing: 1.7px;
                cursor: pointer;
                transition: 0.5s;
                position: relative;
                z-index:10;

                &.on{
                    font-weight: bold;
                    color:#fff;

                    &::before{
                        content: '';
                        display: block;
                        width: 10px;
                        height: 10px;
                        position: absolute;
                        top: 50%;
                        transform: translateY(-50%);
                        left: -20px;
                        color:#eee;
                        background: #eee;
                    }
                }
            }
        }

        
            
        > .inner{
            width: 100%;
            height: 100%;
