            top:5%;
            transform: translateX(-50%);
            display: flex;
            justify-content: space-between;


            .notice{
                width: 30%;
                height: 100%;
                padding:500px 0px 150px;
                
                &.anioff{

                    .noticeAni{
                        display: block;
                        opacity: 1;
                    }

                    .noticeMain{
                        display: none;
                        opacity: 0;
                    }


                }

                &.aniOn{

                    >h2{
                        z-index:33;
                        color:rgba(0,0,0,.9);
                        left: 50%;
                        transform: translateX(-50%);
                    }

