#service{
    width: 100%;
    background-image: url(../../../public/img/infoBg01.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    padding: 100px 0px 100px;
    filter: saturate(70%);
    overflow: hidden;

    .inner{
        width: 100%;
        margin: 0 auto;

        >strong{
            font:bold 200px/1 'Orbitron';
            color:rgba(255,255,255,.03);
            letter-spacing: 15px;
            position: absolute;
            left: 12.5%;
            top: 12%; 
        }

        >h2{
            font:bold 85px/1 'Orbitron';
            color:rgba(255,255,255,.8);
            letter-spacing: 7px;
            position: absolute;
            left: 13.5%;
            top: 17.5%;  
        }

        .wrap{
            width: 100%;
            height: 1000px;
            padding:300px 0px 30px;

            .services{
                width: 100%;
                height: 100%;
                padding:0px 115px;
                
                .swiper{
                    width: 100%;
                    height: 100%;
                    display: flex;
                    justify-content: space-between;

                    .swiper-button-prev{
                        position: absolute;
                        left: 0;
                        color:#bbb;
                    }

                    .swiper-button-next{
                        position: absolute;
                        right: 0;
                        color:#bbb;
                    }

                    .swiper-wrapper{

                        .swiper-slide{

                            >li{
                                padding:50px 0px 50px 60px;
                                height: 95%;
                                background: #23323b;
                                display: flex;
                                flex-direction: column;
                                justify-content: space-evenly;

                                h3{
                                    font:bold 25px/35px 'Orbitron';
                                    color:#f9bf7cc9;
                                    letter-spacing: 3px;
                                    margin-bottom: 30px;

                                    b{
                                        color:#fab86cc9;
                                        font-size: 35px;
                                    }
                                }

                                ol{

                                    li{
                                        margin-bottom: 20px;

                                        p{
                                            font:normal 10px/1 'Orbitron';
                                            color:rgba(255,255,255,.6);
                                            letter-spacing: 1.5px;
                                            margin-bottom: 10px;
                                            position: relative;

                                            &::before{
                                                content: '';
                                                display: block;
                                                position: absolute;
                                                width: 5px;
                                                height: 5px;
                                                background: #fff;
                                                border-radius: 50%;
                                                left: -15px;
                                                top: 50%;
                                                transform: translateY(-50%);
                                            }
                                        }

                                        span{
                                           font:bold 10px/1 'Orbitron';
                                            color:rgba(255,255,255,.8);
                                            letter-spacing: 1.5px;
                                        }
                                    }
                                }

                                strong{
                                    cursor: pointer;
                                    font:normal 12px/1 'Orbitron';
                                    color:rgba(255,255,255,.8);
                                    letter-spacing: 2px;
                                    position: relative;
                                    width: 95px;

                                    &::before{
                                        content: '';
                                        display: block;
                                        position: absolute;
                                        width: 50px;
                                        height: 1px;
                                        background: #eee;
                                        top: 50%;
                                        transform: translateY(-50%);
                                        right: -50px;
                                        cursor: pointer;
                                    }

                                    &::after{
                                        content: '>';
                                        display: block;
                                        position: absolute;
                                        width: 3px;
                                        height: 3px;
                                        color:#eee;
                                        top:10%;
                                        transform: translateY(-50%);
                                        right: -50px;
                                        cursor: pointer;
                                    }
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}