@import url('https://fonts.googleapis.com/css2?family=Shippori+Antique+B1&family=Zhi+Mang+Xing&display=swap');

#questions{
    width: 100%;
    height: 100%;
    background-image: url(../../../public/img/questionsBg01.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    filter: saturate(70%);
    padding: 200px 0px 150px;
    position: relative;

    
    .custom-shape-divider-bottom-1646247006 {
        position: absolute;
        top:0px;
        left: 0;
        width: 100%;
        overflow: hidden;
        line-height: 0;
        z-index:-1;
    }

    .custom-shape-divider-bottom-1646247006 svg {
        position: relative;
        display: block;
        width: calc(120% + 1.3px);
        height: 300px;
    }

    .custom-shape-divider-bottom-1646247006 .shape-fill {
        fill: #23323b;
        opacity: 0.3;
    }

    .custom-shape-divider-top-1646267501 {
        position: absolute;
        top:300px;
        left: 0;
        width: 100%;
        overflow: hidden;
        line-height: 0;
        z-index:-2;
    }
    
    .custom-shape-divider-top-1646267501 svg {
        position: relative;
        display: block;
        width: calc(127% + 1.3px);
        height: 500px;
        transform: rotateY(180deg);
    }
    
    .custom-shape-divider-top-1646267501 .shape-fill {
        fill: #23323b;
        opacity: 0.15;
    }

    // .custom-shape-divider-top-1646332536 {
    //     position: absolute;
    //     top: -150px;
    //     left: 0;
    //     width: 100%;
    //     overflow: hidden;
    //     line-height: 0;
    //     z-index:-1;
    // }
    
    // .custom-shape-divider-top-1646332536 svg {
    //     position: relative;
    //     display: block;
    //     width: calc(148% + 1.3px);
    //     height: 450px;
    //     transform: rotateY(180deg) rotateZ(-180deg);
    // }
    
    // .custom-shape-divider-top-1646332536 .shape-fill {
    //     fill: #1c2830;
    //     opacity: 0.6;
    //     // filter: grayscale(10%);
    // }


    .explanation10{
        position: absolute;
        right:150px;
        bottom:50px;
        width: 200px;
        height: 200px;

        .exP10{
            font:normal 10px 'Orbitron';
            color:rgba(255,255,255,.5);
            letter-spacing: 1.2px;
            color:#fff;
            width: 200px;
            height: 100%;
            display: block;
            position: absolute;
            transform: rotate(90deg);

        &::before{
            content: '';
            display: block;
            width: 2px;
            height: 80%;
            background: rgba(255,255,255,.5);
            position: absolute;
            bottom:45%;
            right:20%;
            transform: rotate(90deg);
            }

        &::after{
            content: '∨';
            display: block;
            font-size: 20px;
            color:rgba(255,255,255,.5);
            position: absolute;
            bottom:78.5%;
            right:-22%;
            transform: rotate(270deg);
            }
        }
    }

    
    strong{
        position: absolute;
        font:bold 175px/1 'Orbitron';
        color:rgba(255,255,255,.02);
        letter-spacing: 10px;
        top:7.5%;
        left:48%;
    }

    .inner{
        width: 100%;
        height: 100%;

        >.wrap{
            width: 100%;
            margin: 0 auto;
            height: 100%;
            padding:30px 200px 0px;
            display: flex;

            &:hover{
                .qnaPic img{
                    filter: saturate(70%);
                    opacity: 1;
                }
            }

            .qnaList{
                width: 40%;
                height: 100%;
                // border:1px solid red;

                h2{
                    font:bold 85px/1 'Orbitron';
                    color:rgba(255,255,255,.8);
                    letter-spacing: 7px;
                    text-align: center;
                    text-align: left;
                    padding-left: 30px;
                    padding-bottom:30px;
                    border-bottom:0.5px solid #aaa;
                    width: 85%;
                }

                >ul{
                    li{
                        width: 85%;
                        border-bottom:0.5px solid #aaa;

                        .qnaBox{

                            &.on{
    
                                .qnaBtns{
                                    font:bold 15px/1 'Orbitron';
                                    color:#f9bf7c;

                                    b{
                                        font:bold 20px/15px 'Orbitron';
                                        transform: rotate(90deg);
                                        color:#f9bf7c;
                                    }
                                }

                                p{
                                    display: block;
                                }
                            }

                            .qnaBtns{
                                font:bold 14px/1 'Orbitron';
                                color:rgba(255,255,255,.9);
                                letter-spacing: 2.3px;
                                padding:25px 15px;
                                display: block;
                                cursor: pointer;
                                transition:all 1s;
                                b{
                                    font:bold 20px/12.5px 'Orbitron';
                                    color:rgba(255,255,255,.9);
                                    margin-right: 10px;
                                    display: inline-block;
                                }
                            }

                            p{
                                font:normal 10px/15px 'Orbitron';
                                color:rgba(255,255,255,.7);
                                margin-bottom: 30px;
                                letter-spacing: 1.3px;
                                padding-left: 35px;
                                display: none;
                            }
                        }
                    }
                }
            }

            .qnaPic{
                width: 60%;
                height: 650px;
                position: relative;
                
                img{
                    position: absolute;
                    object-fit: cover;
                    filter: saturate(50%);
                    transition: 1s;
                    opacity: 0.7;

                    &:nth-child(1){
                        width: 60%;
                        height: 75%;
                        top: 50px;
                        left: -50px;
                    }

                    &:nth-child(2){
                        width: 52.5%;
                        height: 65%;
                        top: -122.5px;
                        right:-10px;
                        transform:rotate(-1deg);
                    }

                    &:nth-child(3){
                        width: 52.5%;
                        height: 65%;
                        bottom: -50px;
                        right: 80px;
                        transform: rotate(-1deg);
                    }
                }
            }
        }
    }
}
