#info{
    width: 100%;
    height: 100vh;
    background-image: url(../../../public/img/infoBg01.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    filter: saturate(70%);
    position: relative;

    .custom-shape-divider-top-1646267501 {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        overflow: hidden;
        line-height: 0;
        // border:3px solid yellow;
    }
    
    .custom-shape-divider-top-1646267501 svg {
        position: relative;
        display: block;
        width: calc(127% + 1.3px);
        height: 500px;
        transform: rotateY(180deg);
    }
    
    .custom-shape-divider-top-1646267501 .shape-fill {
        fill: #23323b;
        opacity: 0;
    }

    .custom-shape-divider-bottom-1646247006 {
        position: absolute;
        top: -350px;
        left: 0;
        width: 100%;
        overflow: hidden;
        line-height: 0;
    }

    .custom-shape-divider-bottom-1646247006 svg {
        position: relative;
        display: block;
        width: calc(100% + 1.3px);
        height: 250px;
        transform: rotateZ(-180deg);
    }

    .custom-shape-divider-bottom-1646247006 .shape-fill {
        fill: #23323b;
        opacity: 0.3;
    }

    strong{
        position: absolute;
        font:bold 175px/1 'Orbitron';
        color:rgba(255,255,255,.03);
        letter-spacing: 7px;
        top: -200px;
        left: 50%;
    }

    .inner{
        height: 100%;
        width: 1180px;
        margin: 0 auto;
        position: relative;
        padding:50px 0px;

        h2{
            font:normal 75px/1 'Orbitron';
            color:rgba(255,255,255,.9);
            letter-spacing: 5px;
            text-align: center;
            margin-bottom: 20px;
        }

        .wrap{
            width: 100%;
            height: 100%;
            // border:3px solid blue;

            .mySwiper2{
                width: 70%;
                height: 65%;
                margin-bottom: 50px;
                background: rgba(0,0,0,.09);
                box-shadow: 5px 5px 10px rgba(0,0,0,.9);
                border:1px solid #232323;

                .swiper-pagination{
                    top: 30px;
                    width: 100%;
                    height: 30px;
                    
                    .swiper-pagination-bullet{
                        color:#fff;
                        background: #fff;
                        width:15px;
                        height: 15px;
                        border-radius: 0px;
                        margin: 0px 10px;
                    }
                }
                
                .swiper-slide{
                    height: 100%;
                    width: 100%;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    color:#fff;
                    padding:75px 125px;

                    &:hover{
                        img{
                            filter: grayscale(70%);
                        }

                        .svg-inline--fa{
                            opacity: 1;
                        }
                    }

                    b{
                        font:bold 13px/1 'Orbitron';
                        color:rgba(255,255,255,.8);
                        letter-spacing: 3px;
                        position: absolute;
                        top: 0;
                    }
                    
                    img{
                        height: 100%;
                        width: 100%;
                        object-fit: cover;
                        filter: grayscale(100%);
                        opacity: 0.5;
                        transition: 1s;
                    }

                    .svg-inline--fa{
                        font-size: 30px;
                        width: 50px;
                        height: 50px;
                        border:1px solid red;
                        position: absolute;
                        padding:15px 15px 15px 20px;
                        border-radius: 50%;
                        color:rgba(255,255,255,.8);
                        text-align: center;
                        line-height: 50px;
                        border:5px solid #ccc;
                        cursor: pointer;
                        opacity: 0;
                        transition: 1s;
                    }

                    h3{
                        font:normal 22px/1 'Orbitron';
                        color:rgba(255,255,255,.6);
                        letter-spacing: 5px;
                        position: absolute;
                        bottom: 30px;
                    }
                }
            }

            .mySwiper{
                width: 100%;
                height: 150px;
                // border:3px solid yellow;

                .swiper-slide{
                    // border:1px solid #fff;
                    height: 100%;
                    text-align: center;
      

                    img{
                        width: 75%;
                        height: 100%;
                        object-fit: cover;
                        text-align: center;
                        filter: saturate(30%);
                        opacity: 0.7;
                        transition: 1s;
                        box-shadow:5px 5px 5px rgba(0,0,0,.9);
                        cursor: pointer;

                        &:hover{
                            filter: saturate(70%);
                            opacity: 0.9;
                            transform: scale(1.05);
                        }
                    }
                }
            }
        }
    }
}
