@import url('https://fonts.googleapis.com/css2?family=Dancing+Script&display=swap');

#story{
    width: 100%;
    height: 100%;
    background-image: url(../../../public/img/storyBg01.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    filter: saturate(70%);
    padding-top:100px;
    padding-bottom:275px;
    position: relative;

    .explanation08{
        position: absolute;
        right:200px;
        bottom:50px;
        width: 200px;
        height: 200px;

        .exP8{
            font:normal 10px 'Orbitron';
            color:rgba(255,255,255,.5);
            letter-spacing: 1.2px;
            color:#fff;
            width: 200px;
            height: 100%;
            display: block;
            position: absolute;
            transform: rotate(90deg);

        &::before{
            content: '';
            display: block;
            width: 2px;
            height: 80%;
            background: rgba(255,255,255,.5);
            position: absolute;
            bottom:45%;
            right:20%;
            transform: rotate(90deg);
            }

        &::after{
            content: '∨';
            display: block;
            font-size: 20px;
            color:rgba(255,255,255,.5);
            position: absolute;
            bottom:78.5%;
            right:-22%;
            transform: rotate(270deg);
            }
        }
    }

    >strong{
        font:bold 100px/1 'Orbitron';
        color:rgba(255,255,255,.7);
        text-shadow: 0px 0px 3px rgba(255,255,255,.1);
        letter-spacing: 10px;
        position: absolute;
        transform: rotate(270deg);
        left:-100px;
        top: 40%;
    }

    .inner{
        width: 1180px;
        height: 100%;
        margin: 0 auto;

        >.svg-inline--fa{
            font-size: 50px;
            color:#eee;
            position: absolute;
            left: 50%;
            transform: translateX(-50%);
            z-index:10;
            top: 120px;
            opacity: 0.9;
        }

        .wrap{
            width: 100%;
            height: 100%;

            .storyGroup{
                width: 100%;
                height: 600px;
                display: flex;
                overflow: hidden;
                transition: 1.5s;

                &.on{
                    height: 1050px;
                }


                .storyPic{
                    width: 50%;
                    height: 1000px;

                    img{
                        width: 100%;
                        height: 100%;
                        object-fit: cover;
                        filter: saturate(50%);
                    }
                }
        
                .storyContent{
                    height: 1000px;
                    width: 50%;
                    padding:75px 50px;

                    h3{
                        font:bold 30px/35px 'Orbitron';
                        color:rgba(255,255,255,.9);
                        letter-spacing: 5px;
                        margin-bottom: 35px;
                    }

                    p{
                        font:normal 10px/20px 'Orbitron';
                        color:rgba(255,255,255,.7);
                        letter-spacing: 1.7px;
                        margin-bottom: 50px;
                    }

                    strong{
                        font:bold 22.5px/25px 'Dancing Script';
                        color:rgba(255,255,255,.8);
                        letter-spacing: 1.3px;
                        width: 50%;
                        display: block;
                        margin-bottom: 50px;
                    }

                    span{
                        font:bold 10px/1 'Orbitron';
                        color:rgba(255,255,255,.9);
                        letter-spacing: 3px;
                        text-align: right;
                        display: block;
                    }

                    .fa-angle-down{
                        position: relative;
                        font-size: 50px;
                        top: -50.5%;
                        left: 50%;
                        transform: translateX(-50%);
                        color:rgba(255,255,255,.7);
                        cursor: pointer;
                        animation: angleAni 10s;
                        animation-fill-mode: forwards;

                        &:hover{
                            opacity: 1;
                        }

                        &.off{
                            opacity: 0;
                            display: none;
                        }
                    }

                    .fa-angle-up{
                        position: relative;
                        font-size: 50px;
                        left: 50%;
                        transform: translateX(-50%);
                        color:rgba(255,255,255,.7);
                        opacity: 0;
                        display: none;
                        cursor: pointer;
                        transition: 3s;

                        &:hover{
                            opacity: 1;
                        }

                        &.on{
                            opacity: 0.8;
                            display: block;
                        }
                    }
                }
            }
        }
    }
}

@keyframes angleAni {
    0%{opacity: 0;}
    100%{opacity: 0.8;}
}